}

.brand-nav .nav-dropdown-trigger.has-selection::before {
    content: '';
    position: absolute;
    bottom: 1px;
    left: 0;
    right: 0;
    height: 1.5px;
    background: linear-gradient(
        90deg,
        transparent,
        rgba(0, 255, 170, 0.8),
        transparent
    );
    opacity: 1;
    transform: scaleX(1);
    box-shadow: 0 0 16px rgba(0, 255, 170, 0.6);
}

.brand-nav .nav-dropdown-trigger::after {
